import React from "react";
import styled from "styled-components";

const Wrapper = styled.section`
  flex: 1;
  padding: 1rem;
  max-width: ${props => props.collapsed ? "10%" : "initial"};
  cursor: ${props => props.collapsed ? "pointer" : "initial"};
  position: relative;
  & h2 {
    font-size: 1rem;
  }
`

const CloseButton = styled.span`
  position: absolute;
  top: 1.5rem;
  right: 1rem;
  font-weight: bold;
  transform: rotate(45deg);
  font-size: 2rem;
  cursor: pointer;
`

const noop = () => {}

const Section = ({ children, className, collapsed = true, toggleSection }) => {
return (
    <Wrapper className={className} collapsed={collapsed} onClick={collapsed ? toggleSection : noop}>
      {!collapsed && <CloseButton onClick={toggleSection}>+</CloseButton>}
      {children}
    </Wrapper>
  )
}

export default Section;